import {
  HttpClient,
  HttpErrorResponse,
  HttpEventType,
  HttpRequest,
  HttpResponse,
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
@Injectable({
  providedIn: 'root',
})
export class ComService {
  // 错误消息提示
  alert: { msg: string; show: boolean } = { msg: '', show: false };
  ws: any;
  data: any = {};
  previewOption: { isShowMask: boolean; src?: string } = {
    isShowMask: false,
  };

  api = {
    login: '/api/auth/login', //post
    register: '/api/auth/register', //post
    message: '/api/message/', //post
    polling: '/api/message/polling', //get
    history: '/api/message/record', //get
    userList: '/api/user/list', // get
    logout: '/api/auth/logout', //post
    userInfo: '/api/user/', //{uid},get
    files: '/file/', // 上传文件
  };
  isLogin: boolean = true;
  constructor(private http: HttpClient, private router: Router) {
    this.windowActive();
  }
  isWindowActive: boolean = true;
  // 格式化时间
  formatTime(
    date: string | number | Date = '' /* 日期，可选默认今天 */,
    fmt = 'YYYY-MM-DD' /*格式字符串默认YYYY-MM-DD hh:mm:ss */
  ) {
    //兼容ios
    if (typeof date === 'string') {
      date = date.replace(/-/g, '/');
    }
    date = date ? new Date(date) : new Date();
    var o = {
      'Y+': date.getFullYear(), //年
      'M+': date.getMonth() + 1, //月份
      'D+': date.getDate(), //日
      'h+': date.getHours(), //小时
      'm+': date.getMinutes(), //分
      's+': date.getSeconds(), //秒
      'q+': Math.floor((date.getMonth() + 3) / 3), //季度
      S: date.getMilliseconds(), //毫秒
    };
    if (/(Y+)/.test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (o['Y+'] + '').substr(4 - RegExp.$1.length));
    }
    for (var k in o) {
      if (new RegExp('(' + k + ')').test(fmt)) {
        fmt = fmt.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ('00' + o[k]).substr(('' + o[k]).length)
        );
      }
    }
    return fmt;
  }
  timeFromNow(time) {
    //时间转换
    const timestamp = Date.now();
    const createTime = new Date(time);
    time = createTime.getTime();
    const diff = timestamp - time;
    if (diff < 60 * 1000) {
      return '';
    } else if (diff < 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 1000)) + '分钟前';
    } else if (diff < 24 * 60 * 60 * 1000) {
      return Math.floor(diff / (60 * 60 * 1000)) + '小时前';
    } else {
      return this.formatTime(time, 'YYYY-MM-DD hh:mm');
    }
  }
  windowActive() {
    var hiddenProperty =
      'hidden' in document
        ? 'hidden'
        : 'webkitHidden' in document
        ? 'webkitHidden'
        : 'mozHidden' in document
        ? 'mozHidden'
        : null;
    var visibilityChangeEvent = hiddenProperty.replace(
      /hidden/i,
      'visibilitychange'
    );
    var onVisibilityChange = function () {
      if (document[hiddenProperty]) {
        // 未激活状态！-发送桌面通知
        this.isWindowActive = true;
      } else {
        // '激活状态！
        this.isWindowActive = false;
      }
    };
    document.addEventListener(visibilityChangeEvent, onVisibilityChange);
    window.onblur = () => {
      this.isWindowActive = false;
    };
    window.onfocus = () => {
      this.isWindowActive = true;
    };
  }
  showAlert(msg) {
    this.alert.show = true;
    this.alert.msg = msg;
    setTimeout(() => {
      this.alert.show = false;
    }, 2000);
  }

  doHttpResponse(res, success, fail?) {
    let code = res.code;
    if (code) {
      if (code === 3 || code === 6) {
        // 未登录
        this.isLogin = false;
        this.showAlert('登录失效，请重新登录');
        setTimeout(() => {
          this.router.navigate(['/auth/login']);
        }, 2000);
      } else {
        fail ? fail(res) : this.showAlert(res.msg);
      }
    } else {
      // 成功
      success && success(res.data);
    }
  }
  // 发送消息
  sendMsg(msg, success?, fail?) {
    this.http
      .post(this.api.message, msg)
      .pipe(
        catchError(
          this.handleError('sendMsg', () => {
            fail && fail();
          })
        )
      )
      .subscribe((res) => {
        this.doHttpResponse(
          res,
          (data) => {
            success && success(data);
          },
          fail
        );
      });
  }
  private handleError(uri: string, failCallback?) {
    return (error: HttpErrorResponse) => {
      console.dir(error);
      if (failCallback) {
        setTimeout(() => {
          failCallback();
        }, 3000);
      } else {
        this.showAlert('http request ' + uri + ' error ');
      }

      if (error.error instanceof ErrorEvent) {
        // A client-side or network error occurred. Handle it accordingly.
        console.error(
          'A client-side or network error occurred. Handle it accordingly.'
        );
        this.showAlert('网络错误');
      } else {
        // The backend returned an unsuccessful response code.
        // The response body may contain clues as to what went wrong.
        console.error(
          `Backend returned code ${error.status}, ` + `body was: ${error.error}`
        );
        this.showAlert('后端服务器错误');
      }
      // Return an observable with a user-facing error message.
      return throwError('Something bad happened; please try again later.');
    };
  }
  // 开启轮询
  pollingStart(obj, random: string, domain?: string) {
    let options: any = {
      observe: 'response',
    };

    this.http
      .get(
        this.api.polling + '?random=' + random + (domain ? '&' + domain : ''),
        options
      )
      .pipe(
        catchError(
          this.handleError('polling', () => {
            this.pollingStart(obj, random, domain);
          })
        )
      )
      .subscribe((res: any) => {
        if (!obj.myID) {
          obj.myID = Number(res.headers.get('Uid'));
        }
        // 新建立的连接，要渲染历史记录
        let isNewConnect = res.headers.get('Request-Times') === '1';
        let ret = res.body;
        let isServer = 'rid' in obj;
        this.doHttpResponse(ret, (data) => {
          if (isNewConnect) {
            if (!isServer) {
              data.forEach((d) => {
                this.attachSPT(obj, d, isServer);
              });

              obj.chatList = data || [];
              setTimeout(() => {
                obj.toBottom();
              }, 500);
            }
          } else {
            // 渲染数据到聊天窗口
            data && this.doPolling(data, obj, isServer);
          }
        });
        // 获取到数据-直接轮询
        this.isLogin && this.pollingStart(obj, random, domain);
      });
  }
  // 图片预览
  preview(file, callback) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (e) => {
      callback(e.target.result);
    };
  }
  // 上传文件
  uploadFile(fileObj, callback, progressCallback?) {
    const formData = new FormData();
    formData.append('file', fileObj.file);
    let api = 'https://io.imdraw.com:444';
    const req = new HttpRequest('POST', api, formData, {
      reportProgress: true,
    });
    this.http
      .request(req)
      .pipe(
        catchError(
          this.handleError('uploadFile', () => {
            callback(fileObj, true);
            this.showAlert('文件服务器故障!');
          })
        )
      )
      .subscribe((event) => {
        if (event.type === HttpEventType.UploadProgress) {
          fileObj.uploadProgress = Math.round(
            (100 * event.loaded) / event.total
          );
          progressCallback && progressCallback(fileObj);
        } else if (event instanceof HttpResponse) {
          let body: any = event.body;
          fileObj.src = api + '/' + body.key;
          fileObj.type === 'file' && (fileObj.src += '?operation=download');
          callback(fileObj);
        }
      });
  }

  debounce(fn, delay = 1000) {
    var timeout;
    return function () {
      clearTimeout(timeout);
      timeout = setTimeout(fn.bind(this, ...arguments), delay);
    };
  }
  // fid 接收者=我 右， 不是我 左
  /**
   *
   * @param data
   * @param obj
   * @param isServer
   */
  doPolling(data, obj, isServer?) {
    console.log('doPolling: ', data);

    data.forEach((d) => {
      let fid = d.fid;
      let isISend = fid === obj.myID;

      if (d.type === 3) {
        isISend &&
          this.showAlert(
            '游客' + d.fid + (d.data === 'online' ? '上' : '离') + '线'
          );
        // 客服端 处理一下用户列表用户上下线显示
        if (isServer) {
          let i = obj.userList.findIndex((x) => x.id === d.fid);

          i > -1 && (obj.userList[i].state = d.data === 'online');
          console.log('type=3', i, d, obj.userList[i].state);
        }
      } else {
        this.attachSPT(obj, d, isServer);
        if (!isISend) {
          // 不是我发送的消息-判断是客服端还是服务端
          if (isServer) {
            // 客服端-判断发送方是正在谈话的一方吗
            let isCuUserSend = fid === (obj.user && obj.user.id);
            d.state = true;
            if (isCuUserSend) {
              obj.chatList.push(d);
              let name =
                obj.user && obj.user.name
                  ? '用户' + obj.user.name
                  : '游客' + fid;
              !this.isWindowActive &&
                this.deskNotify('消息提示', name + '发来一条新消息');
            } else {
              // 不是当前用户发来的消息-判断该用户是否已经在用户列表了
              let userIndex = obj.userList.findIndex((u) => u.id === fid);
              let isInUserList = userIndex > -1;
              if (isInUserList) {
                let name =
                  obj.userList[userIndex] && obj.userList[userIndex].name
                    ? '用户' + obj.userList[userIndex].name
                    : '游客' + fid;
                this.deskNotify('消息提示', name + '发来一条新消息');
              } else {
                // 不在用户列表-获取用户信息-推送用户到列表

                this.getUserInfo(fid, (userInfo) => {
                  obj.userList.unshift(userInfo);
                  let name = userInfo.name
                    ? '用户' + userInfo.name
                    : '游客' + fid;
                  d.state = true;
                  !this.isWindowActive &&
                    this.deskNotify('消息提示', name + '发来一条新消息');
                });
              }
            }
          } else {
            // 客户端-显示在左侧
            obj.chatList.push(d);
            !this.isWindowActive &&
              this.deskNotify('消息提示', '有一条新消息回复');
          }
        } else {
          // 是我发送的，先看在不在列表中，不在的话就推到列表中

          let i = obj.chatList.findIndex((x) => x.mark === d.mark);
          i === -1 && obj.chatList.push(d);
        }
      }
    });
    obj.toBottom();
  }
  // 获取用户列表
  getUserList(success) {
    this.http
      .get(this.api.userList)
      .pipe(catchError(this.handleError('getUserList')))
      .subscribe((res) => {
        this.doHttpResponse(res, success);
      });
  }
  getUserInfo(id, cb) {
    this.http
      .get(this.api.userInfo + id)
      .pipe(catchError(this.handleError('getUserInfo')))
      .subscribe((res: any) => {
        this.doHttpResponse(res, cb);
      });
  }
  login(data) {
    this.http
      .post(this.api.login, data)
      .pipe(catchError(this.handleError('login')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('登录成功');
          this.isLogin = true;
          // 登录成功跳转客服页面
          setTimeout(() => {
            this.router.navigate(['/server']);
          }, 2000);
        });
      });
  }
  register(data) {
    this.http
      .post(this.api.register, data)
      .pipe(catchError(this.handleError('register')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('注册成功，马上跳转登录');
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        });
      });
  }

  logout() {
    this.http
      .post(this.api.logout, null)
      .pipe(catchError(this.handleError('logout')))
      .subscribe((res) => {
        this.doHttpResponse(res, () => {
          this.showAlert('登出成功!');
          setTimeout(() => {
            this.router.navigate(['/auth/login']);
          }, 2000);
        });
      });
  }
  getHistory(obj, id, success) {
    let options: any = {
      observe: obj.myID ? 'body' : 'response',
    };
    let isResponseObserv = options.observe === 'response';
    let isServer = 'rid' in obj;
    this.http
      .get(this.api.history + (id ? '?id=' + id : ''), options)
      .pipe(catchError(this.handleError('getHistory')))
      .subscribe((res: any) => {
        if (!obj.myID && isResponseObserv) {
          obj.myID = Number(res.headers.get('Uid'));
        }
        let ret = isResponseObserv ? res.body : res;
        this.doHttpResponse(ret, (data) => {
          data && data.reverse();
          data &&
            data.forEach((d) => {
              this.attachSPT(obj, d, isServer);
            });

          success(data);
        });
      });
  }

  attachSPT(obj, d, isServer) {
    let isIsend = d.fid === obj.myID;

    d.isServer = isServer ? isIsend : !isIsend;
    d.position = !isIsend;
    d.timeStr = this.timeFromNow(d.time / 1000000);
    d.timeFormat = this.formatTime(d.time / 1000000, 'YYYY-MM-DD hh:mm:ss');
  }
  timer: any;
  notifyInstanceEvent(instance) {
    instance.onclick = function (e) {
      console.log('click: ', e);
      window.focus();
      instance.close();
    };
    instance.onerror = function () {};
    instance.onshow = function () {};
    instance.onclose = function () {};
  }
  deskNotify(title, msg) {
    let img = '/assets/images/desk-logo.png';
    var Notification = window.Notification;

    if (Notification) {
      //支持桌面通知
      if (Notification.permission == 'granted') {
        //已经允许通知
        var instance = new Notification(title, {
          body: msg,
          icon: img,
          data: 'localhost/server',
        });
        this.notifyInstanceEvent(instance);
      } else {
        //第一次询问或已经禁止通知(如果用户之前已经禁止显示通知，那么浏览器不会再次询问用户的意见，Notification.requestPermission()方法无效)
        Notification.requestPermission(function (status) {
          if (status === 'granted') {
            //用户允许
            var instance = new Notification(title, {
              body: msg,
              icon: img,
            });
            this.notifyInstanceEvent(instance);
          } else {
            //用户禁止
            return false;
          }
        });
      }
    } else {
      //不支持(IE等)
      var index = 0;
      clearInterval(this.timer);
      this.timer = setInterval(function () {
        let titleElem = document.getElementsByName('title')[0];
        if (index % 2) {
          titleElem.innerText = '【　　　】' + title; //这里是中文全角空格，其他不行
        } else {
          titleElem.innerText = '【新消息】' + title;
        }
        index++;

        if (index > 20) {
          clearInterval(this.timer);
        }
      }, 500);
    }
  }
}
