import { Component, OnInit, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ComService } from '../core/com.service';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.css'],
})
export class ClientComponent implements OnInit {
  constructor(private com: ComService, private route: ActivatedRoute) {}
  chatList:any = [];
  ws:any;
  myID:number;
  chatElem :any;
  keepEnd:boolean = false;
  // 文件列表
  files :any []=[] ;
  // 编辑框数组
  editData:any =[];
  cuFile:any ;
  unread:number=0;
  ngOnInit(): void {
    // domain=xx&mall=xxx
    let search =  window.location.search.substring(1);  
    let random = Date.now().toString();
    this.com.pollingStart(this,random,search);
    
    this.chatElem = document.getElementById('chat');    
  }
  
  getHistory(){
      this.com.getHistory(this,null,(data)=>{
      this.chatList = data || [];
       setTimeout(()=>{
        this.toBottom();  
      },500)        
    })
  }
  toBottom(){
     this.chatElem.scrollTop = this.chatElem.scrollHeight  ;
  }
  
  sendListen(content){    
    content.isServer = false;
    content.position= false;
    this.chatList.push(content);
  }
  fileEmiter(e){
    this.keepEnd = true;
  }
 
}
